import Canvas from '../helpers/canvas';
import {
  MATRIX_SIZES,
  DEFAULT_MAIN_COLOR,
  DEFAULT_SECOND_COLOR,
  TOOL_IDS,
  DEFAULT_ALPHA,
} from '../constants';
import { actionTypes } from './actions';

const localStorageState = JSON.parse(window.sessionStorage.getItem('piskel'));

export const initialState = {
  mainColor: DEFAULT_MAIN_COLOR,
  secondColor: DEFAULT_SECOND_COLOR,
  matrixSize: MATRIX_SIZES['32x32'],
  layersTextList: ['Layer 1'],
  globalAlphaList: [DEFAULT_ALPHA],
  activeLayer: 0,
  layersCount: 1,
  reDrawFlag: false,
  activeCanvasIndex: 0,
  activePixel: null,
  activeToolId: TOOL_IDS.PEN,
  mouseDownPixel: null,
  mouseUpPixel: null,
  fps: 12,
  user: null,
  ...localStorageState,
  canvasList: localStorageState
    ? localStorageState.canvasList.map(
      canvas => new Canvas(localStorageState.matrixSize).createMatrix().unSerialize(canvas),
    )
    : [new Canvas(MATRIX_SIZES['32x32']).createMatrix()],
};

export const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_MAIN_COLOR: {
      state.canvasList.forEach((canvas) => {
        canvas.setMainColor(action.color);
      });

      return {
        ...state,
        mainColor: action.color,
      };
    }

    case actionTypes.SET_SECOND_COLOR: {
      state.canvasList.forEach((canvas) => {
        canvas.setSecondColor(action.color);
      });

      return {
        ...state,
        secondColor: action.color,
      };
    }

    case actionTypes.SWAP_COLOR: {
      const mainColor = state.secondColor;
      const secondColor = state.mainColor;

      state.canvasList.forEach((canvas) => {
        canvas.setMainColor(mainColor);
        canvas.setSecondColor(secondColor);
      });

      return {
        ...state,
        mainColor,
        secondColor,
      };
    }

    case actionTypes.SET_ACTIVE_PIXEL: {
      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.setActivePixel(action.activePixel);
      return {
        ...state,
        activePixel: action.activePixel,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.REMOVE_ACTIVE_PIXEL: {
      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.removeActivePixel();

      return {
        ...state,
        activePixel: null,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.SET_ACTIVE_TOOL_ID: {
      state.canvasList.forEach((canvas) => {
        canvas.setActiveToolId(action.activeToolId);
      });

      return {
        ...state,
        activeToolId: action.activeToolId,
      };
    }

    case actionTypes.SET_MOUSE_DOWN_PIXEL: {
      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.setMouseDownPixel(action.mouseDownPixel);
      const mainColor = activeCanvas.getMainColor();
      state.canvasList.forEach((canvas) => {
        canvas.setMainColor(mainColor);
      });

      return {
        ...state,
        mouseDownPixel: action.mouseDownPixel,
        mainColor,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.SET_MOUSE_UP_PIXEL: {
      const activeCanvas = state.canvasList[state.activeCanvasIndex];
      activeCanvas.setMouseUpPixel(action.mouseUpPixel);

      return {
        ...state,
        mouseDownPixel: null,
        mouseUpPixel: action.mouseUpPixel,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.UPDATE_CANVAS_LIST: {
      return {
        ...state,
        canvasList: action.canvasList,
      };
    }

    case actionTypes.SET_ACTIVE_CANVAS_INDEX: {
      return {
        ...state,
        activeCanvasIndex: action.activeCanvasIndex,
      };
    }

    case actionTypes.ADD_CANVAS: {
      const newCanvas = new Canvas(state.matrixSize).createMatrix(state.globalAlphaList);
      newCanvas.setMainColor(state.mainColor);
      newCanvas.setSecondColor(state.secondColor);
      newCanvas.setActiveToolId(state.activeToolId);
      newCanvas.setActiveLayer(state.activeLayer);

      const canvasList = [...state.canvasList, newCanvas];

      return {
        ...state,
        canvasList,
        activeCanvasIndex: canvasList.length - 1,
      };
    }

    case actionTypes.DELETE_CANVAS: {
      const canvasList = [...state.canvasList];
      canvasList.splice(action.index, 1);

      const activeCanvasIndex = canvasList.length - 1;

      return {
        ...state,
        canvasList,
        activeCanvasIndex,
      };
    }

    case actionTypes.COPY_CANVAS: {
      const copyCanvas = state.canvasList[action.index];
      const canvasList = [...state.canvasList, copyCanvas.getCopy()];

      const activeCanvasIndex = canvasList.length - 1;
      const activeCanvas = canvasList[activeCanvasIndex];
      activeCanvas.setMainColor(state.mainColor);
      activeCanvas.setSecondColor(state.secondColor);
      activeCanvas.setActiveToolId(state.activeToolId);

      return {
        ...state,
        canvasList,
        activeCanvasIndex,
      };
    }

    case actionTypes.SET_FPS: {
      return {
        ...state,
        fps: action.fps,
      };
    }

    case actionTypes.SET_USER: {
      return {
        ...state,
        user: action.user,
      };
    }

    case actionTypes.DELETE_USER: {
      return {
        ...state,
        user: null,
      };
    }

    case actionTypes.SET_MATRIX_SIZE: {
      state.canvasList.forEach((canvas) => {
        canvas.setMatrixSize(action.matrixSize);
      });

      return {
        ...state,
        matrixSize: action.matrixSize,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.SET_ACTIVE_LAYER: {
      state.canvasList.forEach((canvas) => {
        canvas.setActiveLayer(action.activeLayer);
      });

      return {
        ...state,
        activeLayer: action.activeLayer,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.ADD_LAYER: {
      const globalAlphaList = [...state.globalAlphaList];
      const layersTextList = [...state.layersTextList];
      const { activeLayer } = action;
      globalAlphaList.splice(activeLayer, 0, DEFAULT_ALPHA);
      layersTextList.splice(activeLayer, 0, `Layer ${state.layersCount + 1}`);

      state.canvasList.forEach((canvas) => {
        canvas.addLayer(activeLayer, globalAlphaList);
      });

      return {
        ...state,
        layersTextList,
        globalAlphaList,
        activeLayer,
        layersCount: state.layersCount + 1,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.DELETE_LAYER: {
      const globalAlphaList = [...state.globalAlphaList];
      const layersTextList = [...state.layersTextList];
      const { activeLayer } = action;
      globalAlphaList.splice(activeLayer, 1);
      layersTextList.splice(activeLayer, 1);

      state.canvasList.forEach((canvas) => {
        canvas.deleteLayer(activeLayer, globalAlphaList);
      });

      return {
        ...state,
        layersTextList,
        globalAlphaList,
        activeLayer: 0,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.UP_LAYER: {
      const globalAlphaList = [...state.globalAlphaList];
      const layersTextList = [...state.layersTextList];
      const { activeLayer } = action;
      globalAlphaList.splice(activeLayer - 1, 0, globalAlphaList.splice(activeLayer, 1)[0]);
      layersTextList.splice(activeLayer - 1, 0, layersTextList.splice(activeLayer, 1)[0]);

      state.canvasList.forEach((canvas) => {
        canvas.upLayer(activeLayer, globalAlphaList);
      });

      return {
        ...state,
        layersTextList,
        globalAlphaList,
        activeLayer: activeLayer - 1,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.DOWN_LAYER: {
      const globalAlphaList = [...state.globalAlphaList];
      const layersTextList = [...state.layersTextList];
      const { activeLayer } = action;
      globalAlphaList.splice(activeLayer + 1, 0, globalAlphaList.splice(activeLayer, 1)[0]);
      layersTextList.splice(activeLayer + 1, 0, layersTextList.splice(activeLayer, 1)[0]);

      state.canvasList.forEach((canvas) => {
        canvas.downLayer(activeLayer, globalAlphaList);
      });

      return {
        ...state,
        layersTextList,
        globalAlphaList,
        activeLayer: activeLayer + 1,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    case actionTypes.SET_GLOBAL_ALPHA: {
      const { activeLayer, globalAlpha } = action;
      const globalAlphaList = [...state.globalAlphaList];
      globalAlphaList[activeLayer] = globalAlpha;

      state.canvasList.forEach((canvas) => {
        canvas.setGlobalAlphaList(globalAlphaList);
      });

      return {
        ...state,
        globalAlphaList,
        reDrawFlag: !state.reDrawFlag,
      };
    }

    default:
      return state;
  }
};
