/* eslint-disable react/prop-types */
import React from 'react';
import { connect } from 'react-redux';
import cn from 'classnames';
import Slider from '@material-ui/lab/Slider';
import get from 'lodash/get';

import { selectCanvasList, selectFps } from '../../store/selectors';
import { setFpsAction } from '../../store/actions';
import Canvas from '../canvas';

import './Preview.scss';

class Preview extends React.PureComponent {
  state = {
    currentCanvasIndex: 0,
    reDrawFlag: true,
  }

  myReq = null;

  time = Date.now();

  componentDidMount() {
    this.myReq = requestAnimationFrame(this.tick);
  }

  componentWillUnmount() {
    cancelAnimationFrame(this.myReq);
  }

  tick = () => {
    const { fps } = this.props;
    const delay = 1000 / fps;
    const now = Date.now();

    if (now - this.time > delay) {
      this.time = now;
      const { canvasList } = this.props;
      this.setState((state) => {
        const newIndex = state.currentCanvasIndex + 1;
        return {
          currentCanvasIndex: newIndex < canvasList.length ? newIndex : 0,
          reDrawFlag: !state.reDrawFlag,
        };
      });
    }

    this.myReq = requestAnimationFrame(this.tick);
  }

  onChangeFps = (_, fps) => {
    const { setFps } = this.props;
    setFps(fps);
  }

  render() {
    const { currentCanvasIndex, reDrawFlag } = this.state;
    const { canvasList, fps } = this.props;
    const {
      layers, matrixSize, globalAlphaList, activeLayer,
    } = get(canvasList, [currentCanvasIndex], canvasList[canvasList.length - 1]);

    return (
      <div className={cn('preview-wrapper')}>
        <div className={cn('fps')}>{`FPS: ${fps}`}</div>
        <Canvas
          layers={layers}
          matrixSize={matrixSize}
          reDrawFlag={reDrawFlag}
          globalAlphaList={globalAlphaList}
          activeLayer={activeLayer}
          size={256}
          skipActiveLayer
        />
        <Slider
          defaultValue={fps}
          aria-labelledby="discrete-slider"
          valueLabelDisplay="auto"
          step={1}
          marks
          min={1}
          max={24}
          onChange={this.onChangeFps}
          value={fps}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({ canvasList: selectCanvasList(state), fps: selectFps(state) });

export default connect(
  mapStateToProps,
  { setFps: setFpsAction },
)(Preview);
