/* eslint-disable react/prop-types */
import React from 'react';
import { connect } from 'react-redux';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import isNil from 'lodash/isNil';

import { selectMatrixSize, selectActivePixel } from '../../store/selectors';
import { setMatrixSizeAction } from '../../store/actions';
import { MATRIX_SIZES } from '../../constants';

import './SizesButtons.scss';

class SizesButtons extends React.PureComponent {
  handleChange = (e) => {
    const { setMatrixSize } = this.props;
    const size = e.target.value;
    setMatrixSize(size);
  }

  render() {
    const { matrixSize, activePixel, state } = this.props;

    return (
      <div className="sizes-buttons-wrapper">
        <div className="showLength">
          {`Length: ${(state.canvasList[state.activeCanvasIndex].getLen())}`}
        </div>
        {!isNil(activePixel) && (
        <div className="showCoordinate">
          {((activePixel.indexRow + 1) < 33
            && (activePixel.indexRow + 1) > -1
            && (activePixel.indexColumn + 1) < 33
            && (activePixel.indexColumn + 1) > -1
          )
          && `X:${activePixel.indexColumn}  Y:${activePixel.indexRow}`}
        </div>
        )}
        <FormControl className="form-control">
          <InputLabel className="input-label" htmlFor="size-simple">Size</InputLabel>
          <Select
            className="select"
            value={matrixSize}
            onChange={this.handleChange}
            inputProps={{
              name: 'size-',
              id: 'size-simple',
            }}
          >
            {Object.keys(MATRIX_SIZES).map(
              key => <MenuItem key={key} value={MATRIX_SIZES[key]}>{key}</MenuItem>,
            )}
          </Select>
        </FormControl>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  matrixSize: selectMatrixSize(state),
  activePixel: selectActivePixel(state),
  state,
});

export default connect(mapStateToProps, { setMatrixSize: setMatrixSizeAction })(SizesButtons);
