export const actionTypes = {
  SET_MAIN_COLOR: 'SET_MAIN_COLOR',
  SET_SECOND_COLOR: 'SET_SECOND_COLOR',
  SWAP_COLOR: 'SWAP_COLOR',
  SET_ACTIVE_PIXEL: 'SET_ACTIVE_PIXEL',
  REMOVE_ACTIVE_PIXEL: 'REMOVE_ACTIVE_PIXEL',
  SET_ACTIVE_TOOL_ID: 'SET_ACTIVE_TOOL_ID',
  SET_MOUSE_DOWN_PIXEL: 'SET_MOUSE_DOWN_PIXEL',
  SET_MOUSE_UP_PIXEL: 'SET_MOUSE_UP_PIXEL',
  UPDATE_CANVAS_LIST: 'UPDATE_CANVAS_LIST',
  SET_ACTIVE_CANVAS_INDEX: 'SET_ACTIVE_CANVAS_INDEX',
  ADD_CANVAS: 'ADD_CANVAS',
  DELETE_CANVAS: 'DELETE_CANVAS',
  COPY_CANVAS: 'COPY_CANVAS',
  SET_FPS: 'SET_FPS',
  SET_USER: 'SET_USER',
  DELETE_USER: 'DELETE_USER',
  SET_MATRIX_SIZE: 'SET_MATRIX_SIZE',
  SET_ACTIVE_LAYER: 'SET_ACTIVE_LAYER',
  ADD_LAYER: 'ADD_LAYER',
  DELETE_LAYER: 'DELETE_LAYER',
  UP_LAYER: 'UP_LAYER',
  DOWN_LAYER: 'DOWN_LAYER',
  SET_GLOBAL_ALPHA: 'SET_GLOBAL_ALPHA',
};

export const setMainColorAction = color => ({ type: actionTypes.SET_MAIN_COLOR, color });

export const setSecondColorAction = color => ({ type: actionTypes.SET_SECOND_COLOR, color });

export const swapColorAction = () => ({ type: actionTypes.SWAP_COLOR });

export const setActivePixel = activePixel => ({ type: actionTypes.SET_ACTIVE_PIXEL, activePixel });

export const removeActivePixel = () => ({ type: actionTypes.REMOVE_ACTIVE_PIXEL });

export const setActiveToolId = activeToolId => ({ type: actionTypes.SET_ACTIVE_TOOL_ID, activeToolId });

export const setMouseDownPixel = mouseDownPixel => ({ type: actionTypes.SET_MOUSE_DOWN_PIXEL, mouseDownPixel });

export const setMouseUpPixel = mouseUpPixel => ({ type: actionTypes.SET_MOUSE_UP_PIXEL, mouseUpPixel });

export const updateCanvasList = canvasList => ({ type: actionTypes.UPDATE_CANVAS_LIST, canvasList });

export const setActiveCanvasIndex = activeCanvasIndex => ({
  type: actionTypes.SET_ACTIVE_CANVAS_INDEX,
  activeCanvasIndex,
});

export const addCanvasAction = () => ({ type: actionTypes.ADD_CANVAS });

export const deleteCanvasAction = index => ({ type: actionTypes.DELETE_CANVAS, index });

export const copyCanvasAction = index => ({ type: actionTypes.COPY_CANVAS, index });

export const setFpsAction = fps => ({ type: actionTypes.SET_FPS, fps });

export const setUserAction = user => ({ type: actionTypes.SET_USER, user });

export const deleteUserAction = () => ({ type: actionTypes.DELETE_USER });

export const setMatrixSizeAction = matrixSize => ({ type: actionTypes.SET_MATRIX_SIZE, matrixSize });

export const setActiveLayerAction = activeLayer => ({ type: actionTypes.SET_ACTIVE_LAYER, activeLayer });

export const addLayerAction = activeLayer => ({ type: actionTypes.ADD_LAYER, activeLayer });

export const deleteLayerAction = activeLayer => ({ type: actionTypes.DELETE_LAYER, activeLayer });

export const upLayerAction = activeLayer => ({ type: actionTypes.UP_LAYER, activeLayer });

export const downLayerAction = activeLayer => ({ type: actionTypes.DOWN_LAYER, activeLayer });

export const setGlobalAlphaAction = (activeLayer, globalAlpha) => ({
  type: actionTypes.SET_GLOBAL_ALPHA,
  activeLayer,
  globalAlpha,
});
