import GIF from 'gif.js';

import { drawCtx } from '../../helpers/draw-ctx';
import { getJson } from '../../helpers/get-json';

export const createGif = (canvasList, fps, matrixSize) => new Promise((resolve, reject) => {
  try {
    const delay = 1000 / fps;
    const gif = new GIF({
      workers: 2,
      quality: 10,
    });
    const canvas = document.createElement('canvas');
    canvas.height = matrixSize;
    canvas.width = matrixSize;

    canvasList.forEach(({
      layers,
      globalAlphaList,
      activeLayer,
    }) => {
      drawCtx({
        canvas,
        matrixSize,
        layers,
        globalAlphaList,
        activeLayer,
        size: matrixSize,
        skipActiveLayer: true,
      });
      gif.addFrame(canvas, {
        delay, copy: true,
      });
    });


    gif.on('finished', (blob) => {
      resolve(blob);
    });

    gif.render();
  } catch (error) {
    reject(error);
  }
});

export const createPng = (canvasList, activeCanvasIndex, matrixSize) => new Promise((resolve, reject) => {
  try {
    const canvas = document.createElement('canvas');
    canvas.height = matrixSize;
    canvas.width = matrixSize;

    const {
      layers,
      globalAlphaList,
      activeLayer,
    } = canvasList[activeCanvasIndex];
    drawCtx({
      canvas,
      matrixSize,
      layers,
      globalAlphaList,
      activeLayer,
      size: matrixSize,
      skipActiveLayer: true,
    });

    canvas.toBlob((blob) => {
      resolve(blob);
    });
  } catch (error) {
    reject(error);
  }
});

export const createPiskel = (
  canvasList,
  fps,
  matrixSize,
  layersTextList,
  globalAlphaList,
) => new Promise((resolve, reject) => {
  try {
    const piskel = {
      modelVersion: 2,
      piskel: {
        name: 'New Piskel',
        description: '',
        fps,
        height: matrixSize,
        width: matrixSize,
        layers: layersTextList.map((name, i) => {
          const canvas = document.createElement('canvas');
          canvas.height = matrixSize;
          canvas.width = matrixSize * canvasList.length;

          canvasList.forEach(({ layers }, index) => {
            drawCtx({
              canvas,
              matrixSize,
              layers: [layers[i]],
              size: matrixSize,
              isClear: false,
              index,
              globalAlphaList: [globalAlphaList[i]],
              activeLayer: 0,
              skipActiveLayer: true,
            });
          });

          const image = canvas.toDataURL('image/png;base64');
          return `{"name":"${name}","opacity":${globalAlphaList[i]},"frameCount":${
            canvasList.length
          },"chunks":[{"layout":[${
            canvasList.map((_, ind) => `[${ind}]`).join(',')
          }],"base64PNG":"${image}"}]}`;
        }),
        hiddenFrames: [],
      },
    };

    const jsonse = JSON.stringify(piskel);
    const blob = new Blob([jsonse], { type: 'application/json' });

    resolve(blob);
  } catch (error) {
    reject(error);
  }
});

export const createOwn = state => new Promise((resolve, reject) => {
  try {
    const jsonse = getJson(state);
    const blob = new Blob([jsonse], { type: 'application/json' });

    resolve(blob);
  } catch (error) {
    reject(error);
  }
});

export const uploadFileToGoogleDrive = (name, mimeType, file) => {
  // https://tanaikech.github.io/2018/08/13/upload-files-to-google-drive-using-javascript/
  const metadata = {
    name, // Filename at Google Drive
    mimeType, // mimeType at Google Drive
  };

  const accessToken = window.gapi.auth.getToken().access_token;
  const form = new FormData();
  form.append('metadata', new Blob([JSON.stringify(metadata)], { type: 'application/json' }));
  form.append('file', file);

  return fetch('https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart&fields=id', {
    method: 'POST',
    headers: new Headers({ Authorization: `Bearer ${accessToken}` }),
    body: form,
  }).then(res => res.json());
};
