/* eslint-disable react/prop-types */
import React from 'react';
import { connect } from 'react-redux';
import cn from 'classnames';
import IconButton from '@material-ui/core/IconButton';
import SwapHorizIcon from '@material-ui/icons/SwapHoriz';
import Popover from '@material-ui/core/Popover';
import { SketchPicker } from 'react-color';

import { selectMainColor, selectSecondColor } from '../../store/selectors';
import { swapColorAction, setMainColorAction, setSecondColorAction } from '../../store/actions';

import './ColorPicker.scss';

class ColorPicker extends React.PureComponent {
  state = {
    isMainColor: true,
    anchorEl: null,
  }

  onSecondColorClick = (event) => {
    this.setState({ anchorEl: event.currentTarget, isMainColor: false });
  }

  onMainColorClick = (event) => {
    this.setState({ anchorEl: event.currentTarget, isMainColor: true });
  }

  handleClose = () => {
    this.setState({ anchorEl: null });
  }

  onChangeColor = ({
    rgb: {
      r, g, b, a,
    },
  }) => {
    const { setMainColor, setSecondColor } = this.props;
    const { isMainColor } = this.state;
    const rgba = `rgba(${r}, ${g}, ${b}, ${a})`;
    if (isMainColor) {
      setMainColor(rgba);
    } else {
      setSecondColor(rgba);
    }
  }

  render() {
    const { mainColor, secondColor, swapColor } = this.props;
    const { anchorEl, isMainColor } = this.state;
    const open = Boolean(anchorEl);
    const id = open ? 'simple-popover' : undefined;

    return (
      <div className="color-picker-wrapper">
        <div
          onClick={this.onSecondColorClick}
          className={cn('color', 'second-color')}
          style={{ backgroundColor: secondColor }}
        />
        <div
          onClick={this.onMainColorClick}
          className={cn('color', 'main-color')}
          style={{ backgroundColor: mainColor }}
        />
        <IconButton
          onClick={swapColor}
          className="swap-button"
          aria-label="Swap"
        >
          <SwapHorizIcon className="swap-icon" fontSize="small" />
        </IconButton>
        <Popover
          id={id}
          open={open}
          anchorEl={anchorEl}
          onClose={this.handleClose}
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          transformOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
        >
          <SketchPicker
            color={isMainColor ? mainColor : secondColor}
            onChangeComplete={this.onChangeColor}
          />
        </Popover>
      </div>
    );
  }
}

const mapStateToProps = state => ({ mainColor: selectMainColor(state), secondColor: selectSecondColor(state) });

export default connect(
  mapStateToProps,
  { swapColor: swapColorAction, setMainColor: setMainColorAction, setSecondColor: setSecondColorAction },
)(ColorPicker);
