import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import noop from 'lodash/noop';
import Button from '@material-ui/core/Button';

import './ButtonTool.scss';

export default class ButtonTool extends React.PureComponent {
  static propTypes = {
    id: PropTypes.string,
    contained: PropTypes.string,
    color: PropTypes.string,
    src: PropTypes.string,
    alt: PropTypes.string,
    isActive: PropTypes.bool,
    onClick: PropTypes.func,
  };

  static defaultProps = {
    id: '',
    contained: '',
    color: '',
    src: '',
    alt: '',
    isActive: false,
    onClick: noop,
  };

  onClick = () => {
    const { id, onClick } = this.props;
    onClick(id);
  }


  render() {
    const {
      contained, color, src, alt, isActive,
    } = this.props;

    return (
      <div className="button-wrapper">
        <Button // TODO disable D&D icon
          variant={contained}
          color={color}
          className={cn('button', { active: isActive })}
          onClick={this.onClick}
        >
          <img src={src} alt={alt} width="60%" height="60%" />
        </Button>
      </div>
    );
  }
}
