/* eslint-disable react/prop-types */
import React from 'react';
import { connect } from 'react-redux';
import cn from 'classnames';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import ControlCameraIcon from '@material-ui/icons/ControlCamera';
import ScrollArea from 'react-scrollbar';

import Canvas from '../canvas';
import {
  selectCanvasList, selectReDrawFlag, selectActiveCanvasIndex,
} from '../../store/selectors';
import {
  updateCanvasList, setActiveCanvasIndex, addCanvasAction, deleteCanvasAction, copyCanvasAction,
} from '../../store/actions';

import './CanvasList.scss';

const placeholder = document.createElement('li');
placeholder.className = 'placeholder';

class CanvasList extends React.PureComponent {
  dragged = null;

  over = null;

  dragStart = (e) => {
    this.dragged = e.currentTarget;
    e.dataTransfer.effectAllowed = 'move';
    e.dataTransfer.setData('text/html', this.dragged);
  }

  dragEnd = () => {
    const { setNewCanvasList, canvasList, setNewActiveCanvasIndex } = this.props;
    this.dragged.style.display = 'block';
    try {
      this.dragged.parentNode.removeChild(placeholder);

      const newCanvasList = [...canvasList];
      const from = Number(this.dragged.dataset.id);
      let to = Number(this.over.dataset.id);
      if (from < to) to -= 1;
      newCanvasList.splice(to, 0, newCanvasList.splice(from, 1)[0]);

      setNewCanvasList(newCanvasList);
      setNewActiveCanvasIndex(to);
    } catch (error) {
      // eslint-disable-next-line no-console
      console.error(error);
    }
  }

  dragOver = (e) => {
    e.preventDefault();
    this.dragged.style.display = 'none';
    if (e.target.className === 'placeholder') return;
    let li = e.target;

    while (li !== e.currentTarget) {
      if (li.tagName === 'LI') {
        this.over = li;
        e.currentTarget.insertBefore(placeholder, li);
      }
      li = li.parentNode;
    }
  }

  onCanvasClick = (e) => {
    e.stopPropagation();
    const { setNewActiveCanvasIndex } = this.props;
    const index = Number(e.currentTarget.dataset.id);
    setNewActiveCanvasIndex(index);
  }

  onDeleteClick = (e) => {
    e.stopPropagation();
    const { deleteCanvas } = this.props;
    const index = Number(e.currentTarget.dataset.id);
    deleteCanvas(index);
  }

  onCopyCanvasClick = (e) => {
    e.stopPropagation();
    const { copyCanvas } = this.props;
    const index = Number(e.currentTarget.dataset.id);
    copyCanvas(index);
  }

  render() {
    const { canvasList, activeCanvasIndex, addCanvas } = this.props;

    return (
      <div className="canvas-list-wrapper">
        <ScrollArea
          speed={0.8}
          className="scroll-area"
          contentClassName="scroll-content"
          horizontal={false}
        >
          <ul className="canvas-list" onDragOver={this.dragOver}>
            {canvasList.map(({
              layers, matrixSize, reDrawFlag, globalAlphaList, activeLayer,
            }, i) => (
              <li
                key={i}
                data-id={i}
                className={cn('item', { active: activeCanvasIndex === i })}
                draggable="true"
                onClick={this.onCanvasClick}
                onDragEnd={this.dragEnd}
                onDragStart={this.dragStart}
              >
                <Canvas
                  layers={layers}
                  matrixSize={matrixSize}
                  reDrawFlag={reDrawFlag}
                  globalAlphaList={globalAlphaList}
                  activeLayer={activeLayer}
                  size={128}
                />
                <span className={cn('index', { active: activeCanvasIndex === i })}>{i + 1}</span>
                <div className="buttons-container">
                  <IconButton
                    onClick={this.onDeleteClick}
                    data-id={i}
                    className={cn('button', 'delete-button', { disabled: canvasList.length === 1 })}
                    disabled={canvasList.length === 1}
                    aria-label="Delete"
                  >
                    <DeleteIcon fontSize="small" />
                  </IconButton>
                  <IconButton
                    onClick={this.onCopyCanvasClick}
                    data-id={i}
                    className={cn('button', 'copy-button')}
                    aria-label="Copy"
                  >
                    <FileCopyIcon fontSize="small" />
                  </IconButton>
                  <IconButton className={cn('button', 'move-button')} aria-label="Move">
                    <ControlCameraIcon fontSize="small" />
                  </IconButton>
                </div>
              </li>
            ))}
            <li data-id={canvasList.length} className={cn('empty-item')} />
          </ul>
          <Button onClick={addCanvas} className="add-button" variant="contained" color="secondary">
            Add
            <AddIcon />
          </Button>
        </ScrollArea>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  canvasList: selectCanvasList(state),
  reDrawFlag: selectReDrawFlag(state),
  activeCanvasIndex: selectActiveCanvasIndex(state),
});

export default connect(mapStateToProps, {
  setNewCanvasList: updateCanvasList,
  setNewActiveCanvasIndex: setActiveCanvasIndex,
  addCanvas: addCanvasAction,
  deleteCanvas: deleteCanvasAction,
  copyCanvas: copyCanvasAction,
})(CanvasList);
