/* eslint-disable react/prop-types */
import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';

import { selectUser } from '../../store/selectors';
import { setUserAction, deleteUserAction } from '../../store/actions';
import { getJson } from '../../helpers/get-json';

import './Header.scss';

// https://developers.google.com/identity/sign-in/web/sign-in#before_you_begin
const CLIENT_ID = '29523551799-vta2mgttim54jq67rupeekj1pfhljgk2.apps.googleusercontent.com';
const API_KEY = 'AIzaSyAkU6FnWmBya_q3WZwlA4qR7sGZ8Ro6tZk';

// Array of API discovery doc URLs for APIs used by the quickstart
const DISCOVERY_DOCS = ['https://www.googleapis.com/discovery/v1/apis/drive/v3/rest'];

// Authorization scopes required by the API; multiple scopes can be
// included, separated by spaces.
// https://developers.google.com/drive/api/v3/reference/permissions/list#try-it
const SCOPES = 'https://www.googleapis.com/auth/drive.file';

class Header extends React.PureComponent {
  state = {
    anchorEl: null,
    isLoadGApi: false,
    isSignedIn: false,
  }

  componentDidMount() {
    this.loadGApi();
    window.addEventListener('beforeunload', this.onBeforeUnload);
  }

  componentWillUnmount() {
    window.removeEventListener('beforeunload', this.onBeforeUnload);
  }

  onBeforeUnload = () => {
    const { state } = this.props;
    window.sessionStorage.setItem('piskel', getJson(state));
  }

  loadGApi = () => {
    // https://developers.google.com/drive/api/v3/quickstart/js
    const script = document.createElement('script');
    script.src = 'https://apis.google.com/js/api.js';

    script.onload = () => {
      window.gapi.load('client:auth2', this.initClient);
    };

    document.body.appendChild(script);
  }

  initClient = () => {
    window.gapi.client.init({
      apiKey: API_KEY,
      clientId: CLIENT_ID,
      discoveryDocs: DISCOVERY_DOCS,
      scope: SCOPES,
    }).then(() => {
      // Listen for sign-in state changes.
      window.gapi.auth2.getAuthInstance().isSignedIn.listen(this.updateSigninStatus);

      // Handle the initial sign-in state.
      this.updateSigninStatus(window.gapi.auth2.getAuthInstance().isSignedIn.get());
    }).catch((error) => {
      // eslint-disable-next-line no-console
      console.error(JSON.stringify(error, null, 2));
    });
  }

  updateSigninStatus = (isSignedIn) => {
    const { setUser } = this.props;
    this.setState({ isLoadGApi: true, isSignedIn });
    if (isSignedIn) {
      // https://developers.google.com/identity/sign-in/web/reference#googleusergetbasicprofile
      const BasicProfile = window.gapi.auth2.getAuthInstance().currentUser.get().getBasicProfile();
      const user = {
        id: BasicProfile.getId(),
        name: BasicProfile.getName(),
        givenName: BasicProfile.getGivenName(),
        familyName: BasicProfile.getFamilyName(),
        imageUrl: BasicProfile.getImageUrl(),
        email: BasicProfile.getEmail(),
      };

      setUser(user);
    }
  }

  handleMenu = (event) => {
    this.setState({ anchorEl: event.currentTarget });
  }

  handleClose = () => {
    this.setState({ anchorEl: null });
  }

  handleAuthorize = () => {
    const { isSignedIn } = this.state;
    const { deleteUser } = this.props;
    if (isSignedIn) {
      window.gapi.auth2.getAuthInstance().signOut();
      deleteUser();
    } else {
      window.gapi.auth2.getAuthInstance().signIn();
    }
    this.setState({ anchorEl: null });
  }

  render() {
    const { anchorEl, isLoadGApi, isSignedIn } = this.state;
    const { location, user } = this.props;
    const open = Boolean(anchorEl);

    return (
      <div className="header">
        <AppBar position="static">
          <Toolbar>
            <Link
              className="link"
              to="/"
            >
              <Button
                variant={location.pathname === '/' ? 'outlined' : 'text'}
                className="button"
              >
                Main
              </Button>
            </Link>
            <Link
              className="link"
              to="/piskel/"
            >
              <Button
                variant={location.pathname === '/piskel/' ? 'outlined' : 'text'}
                className="button"
              >
                Piskel
              </Button>
            </Link>
            <Typography variant="h6" className="title">
              Piskel Clone
            </Typography>
            <div className="account">
              {user ? (
                <Typography variant="body2" className="name">
                  {user.name}
                </Typography>
              ) : null}
              <IconButton
                aria-label="Account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={this.handleMenu}
                color="inherit"
              >
                {user ? <Avatar alt="Remy Sharp" src={user.imageUrl} className="avatar" /> : <AccountCircle />}
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={open}
                onClose={this.handleClose}
              >
                <MenuItem
                  disabled={!isLoadGApi}
                  onClick={this.handleAuthorize}
                >
                  {isSignedIn ? 'Sign Out' : 'Sign In'}
                </MenuItem>
              </Menu>
            </div>
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}

const mapStateToProps = state => ({ user: selectUser(state), state });

export default withRouter(connect(mapStateToProps, {
  setUser: setUserAction,
  deleteUser: deleteUserAction,
})(Header));
