/* eslint-disable react/prop-types */
import React from 'react';
import { connect } from 'react-redux';

import ButtonTool from '../button-tool';
import { selectActiveToolId } from '../../store/selectors';
import { setActiveToolId } from '../../store/actions';
import { TOOL_IDS, KEY_CODE } from '../../constants';
import { TOOLS_CONFIG_LIST } from './constants';

import './Tools.scss';

class Tools extends React.PureComponent {
  componentDidMount() {
    window.document.addEventListener('keydown', this.handleKeyDown);
  }

  componentWillUnmount() {
    window.document.removeEventListener('keydown', this.handleKeyDown);
  }

  handleKeyDown = (e) => {
    const { setActiveId } = this.props;

    if (e.shiftKey && e.keyCode === KEY_CODE.KEY_P) {
      setActiveId(TOOL_IDS.PEN);
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_E) {
      setActiveId(TOOL_IDS.ERASER);
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_S) {
      setActiveId(TOOL_IDS.STROKE);
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_B) {
      setActiveId(TOOL_IDS.BUCKET);
    }
    if (e.shiftKey && e.keyCode === KEY_CODE.KEY_X) {
      setActiveId(TOOL_IDS.BUCKET_SC);
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_C) {
      setActiveId(TOOL_IDS.CIRCLE);
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_R) {
      setActiveId(TOOL_IDS.RECTANGLE);
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_I) {
      setActiveId(TOOL_IDS.DITHERING);
    }
    if (e.shiftKey && e.keyCode === KEY_CODE.KEY_W) {
      setActiveId(TOOL_IDS.SQUARE);
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_G) {
      setActiveId(TOOL_IDS.PIPET);
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_Y) {
      setActiveId(TOOL_IDS.RULE);
    }
  }

  render() {
    const { activeToolId, setActiveId } = this.props;

    return (
      <div className="tools_wrapper">
        {TOOLS_CONFIG_LIST.map(({ id, src, alt }) => (
          <ButtonTool
            key={id}
            id={id}
            contained="contained"
            color="primary"
            src={src}
            alt={alt}
            isActive={activeToolId === id}
            onClick={setActiveId}
          />
        ))}
      </div>
    );
  }
}

const mapStateToProps = state => ({ activeToolId: selectActiveToolId(state) });

const mapDispatchToProps = dispatch => ({
  setActiveId: activeToolId => dispatch(setActiveToolId(activeToolId)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Tools);
