export const getJson = ({
  mainColor,
  secondColor,
  matrixSize,
  canvasList,
  activeToolId,
  fps,
  layersTextList,
  globalAlphaList,
  activeLayer,
  layersCount,
}) => JSON.stringify({
  mainColor,
  secondColor,
  matrixSize,
  canvasList: canvasList.map(canvas => canvas.serialize()),
  activeToolId,
  fps,
  layersTextList,
  globalAlphaList,
  activeLayer,
  layersCount,
});
