/* eslint-disable react/prop-types */
import React from 'react';
import { connect } from 'react-redux';
import ScrollArea from 'react-scrollbar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import Radio from '@material-ui/core/Radio';

import { selectLayersTextList, selectGlobalAlphaList, selectActiveLayer } from '../../store/selectors';
import {
  setActiveLayerAction,
  addLayerAction,
  deleteLayerAction,
  upLayerAction,
  downLayerAction,
  setGlobalAlphaAction,
} from '../../store/actions';
import AlphaButton from '../alpha-button';

import './Layers.scss';

class Layers extends React.PureComponent {
  render() {
    const {
      layersTextList,
      globalAlphaList,
      activeLayer,
      setActiveLayer,
      addLayer,
      deleteLayer,
      upLayer,
      downLayer,
      setGlobalAlpha,
    } = this.props;

    return (
      <div className="layers">
        <ScrollArea
          speed={0.8}
          className="scroll-area"
          contentClassName="scroll-content"
          horizontal={false}
        >
          <List dense className="list">
            {layersTextList.map((layerText, index) => {
              const labelId = `label-${layerText}`;
              return (
                <ListItem key={layerText} button onClick={() => setActiveLayer(index)}>
                  <ListItemIcon>
                    <Radio checked={activeLayer === index} />
                  </ListItemIcon>
                  <ListItemText id={labelId} primary={layerText} />
                  <ListItemSecondaryAction>
                    <AlphaButton
                      title={layerText}
                      alpha={globalAlphaList[index]}
                      onChangeAlpha={value => setGlobalAlpha(index, value)}
                    />
                    <IconButton
                      color="secondary"
                      size="small"
                      onClick={() => downLayer(index)}
                      disabled={index === layersTextList.length - 1}
                    >
                      <ArrowDownwardIcon fontSize="inherit" />
                    </IconButton>
                    <IconButton
                      color="secondary"
                      size="small"
                      onClick={() => upLayer(index)}
                      disabled={index === 0}
                    >
                      <ArrowUpwardIcon fontSize="inherit" />
                    </IconButton>
                    <IconButton
                      color="secondary"
                      size="small"
                      onClick={() => addLayer(index)}
                    >
                      <AddIcon fontSize="inherit" />
                    </IconButton>
                    <IconButton
                      color="secondary"
                      size="small"
                      onClick={() => deleteLayer(index)}
                      disabled={globalAlphaList.length === 1}
                    >
                      <DeleteIcon fontSize="inherit" />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
              );
            })}
          </List>
        </ScrollArea>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  layersTextList: selectLayersTextList(state),
  globalAlphaList: selectGlobalAlphaList(state),
  activeLayer: selectActiveLayer(state),
});

export default connect(mapStateToProps, {
  setActiveLayer: setActiveLayerAction,
  addLayer: addLayerAction,
  deleteLayer: deleteLayerAction,
  upLayer: upLayerAction,
  downLayer: downLayerAction,
  setGlobalAlpha: setGlobalAlphaAction,
})(Layers);
