import forEachRight from 'lodash/forEachRight';

export const drawCtx = ({
  canvas,
  layers,
  size,
  matrixSize,
  temporaryMatrix,
  isClear = true,
  index = 0,
  globalAlphaList,
  activeLayer,
  skipActiveLayer = false,
}) => {
  const ctx = canvas.getContext('2d');

  if (isClear) {
    ctx.clearRect(0, 0, size, size);
  }

  const pixelSize = size / matrixSize;

  forEachRight(layers, (layer, i) => {
    const globalAlpha = globalAlphaList[i];

    ctx.globalAlpha = activeLayer === i || skipActiveLayer ? globalAlpha : globalAlpha * 0.3;

    layer.forEach((row, rowIndex) => {
      row.forEach((pixel, columnIndex) => {
        ctx.fillStyle = pixel.color;
        ctx.fillRect((index * size) + (pixelSize * columnIndex), pixelSize * rowIndex, pixelSize, pixelSize);
        if (temporaryMatrix && i === layers.length - 1) {
          const temporaryPixel = temporaryMatrix[rowIndex][columnIndex];
          ctx.fillStyle = temporaryPixel.color;
          ctx.fillRect((index * size) + (pixelSize * columnIndex), pixelSize * rowIndex, pixelSize, pixelSize);
        }
      });
    });
  });
};
