/* eslint-disable react/prop-types */
import React from 'react';
import { connect } from 'react-redux';
import isEqual from 'lodash/isEqual';

import Canvas from '../canvas';
import { selectActiveCanvas, selectHasMouseDownPixel, selectMatrixSize } from '../../store/selectors';
import {
  setActivePixel, removeActivePixel, setMouseDownPixel, setMouseUpPixel,
} from '../../store/actions';

const SIZE_RATIO = 0.8;
const LEFT_MOUSE_BUTTON_KEY = 0;

class MainCanvas extends React.Component {
  canvas = null;

  state = {
    pixelCoordinate: null,
  }

  componentDidMount() {
    window.document.addEventListener('mousemove', this.onMouseMove);
    window.document.addEventListener('mousedown', this.onMouseDown);
    window.document.addEventListener('mouseup', this.onMouseUp);
  }

  componentWillUnmount() {
    window.document.removeEventListener('mousemove', this.onMouseMove);
    window.document.removeEventListener('mousedown', this.onMouseDown);
    window.document.removeEventListener('mouseup', this.onMouseUp);
  }

  onMouseMove = (event) => {
    if (this.canvas) {
      const { dispatch, hasMouseDownPixel, matrixSize } = this.props;
      const { pixelCoordinate } = this.state;

      const newPixelCoordinate = this.getPixelCoordinate(event);
      const { indexRow, indexColumn } = newPixelCoordinate;

      if (
        !isEqual(newPixelCoordinate, pixelCoordinate)
        && (
          hasMouseDownPixel
          || (
            indexRow < matrixSize
            && indexRow >= 0
            && indexColumn < matrixSize
            && indexColumn >= 0
          )
        )
      ) {
        this.setState({ pixelCoordinate: newPixelCoordinate });
        dispatch(setActivePixel(newPixelCoordinate));
      }
    }
  }

  onMouseDown = (event) => {
    if (this.canvas && event.button === LEFT_MOUSE_BUTTON_KEY) {
      const { dispatch } = this.props;
      const pixelCoordinate = this.getPixelCoordinate(event);
      if (pixelCoordinate != null && event.target.tagName === 'CANVAS') {
        dispatch(setMouseDownPixel(pixelCoordinate));
      }
    }
  }

  onMouseUp = (event) => {
    if (this.canvas && event.button === LEFT_MOUSE_BUTTON_KEY) {
      const { dispatch } = this.props;
      const pixelCoordinate = this.getPixelCoordinate(event);
      if (pixelCoordinate != null) {
        dispatch(setMouseUpPixel(pixelCoordinate));
      }
    }
  }

  onMouseLeave = () => {
    const { dispatch } = this.props;
    dispatch(removeActivePixel());
  }

  getPixelCoordinate = ({ clientX, clientY }) => {
    if (this.canvas) {
      const {
        left, top,
      } = this.canvas.getBoundingClientRect();
      const pixelSize = this.getPixelSize();

      const x1 = Math.floor((clientX - left));
      const y1 = Math.floor((clientY - top));

      const indexColumn = Math.floor(x1 / pixelSize);
      const indexRow = Math.floor(y1 / pixelSize);

      return { indexRow, indexColumn };
    }
    return null;
  }

  getPixelSize = () => {
    const { matrixSize } = this.props;
    const width = window.innerWidth * SIZE_RATIO;
    const height = window.innerHeight * SIZE_RATIO;

    const fractionalSize = width < height ? width : height;
    return Math.round(fractionalSize / matrixSize);
  }

  // TODO add eventLisener 'resize'
  getSize = () => {
    const { matrixSize } = this.props;
    const pixelSize = this.getPixelSize();

    return pixelSize * matrixSize;
  }

  refCanvas = (element) => {
    this.canvas = element;
  }

  render() {
    const {
      layers, temporaryMatrix, matrixSize, reDrawFlag, globalAlphaList, activeLayer,
    } = this.props;
    const size = this.getSize();

    return (
      <div>
        <Canvas
          layers={layers}
          temporaryMatrix={temporaryMatrix}
          matrixSize={matrixSize}
          reDrawFlag={reDrawFlag}
          globalAlphaList={globalAlphaList}
          activeLayer={activeLayer}
          size={size}
          refCanvas={this.refCanvas}
          onMouseLeave={this.onMouseLeave}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  ...selectActiveCanvas(state),
  hasMouseDownPixel: selectHasMouseDownPixel(state),
  matrixSize: selectMatrixSize(state),
});

export default connect(mapStateToProps)(MainCanvas);
