/* eslint-disable no-continue */
import map from 'lodash/map';
import get from 'lodash/get';

import {
  DEFAULT_MAIN_COLOR,
  DEFAULT_SECOND_COLOR,
  ACTIVE_COLOR, TOOL_IDS,
  INITIAL_COLOR,
  DEFAULT_ALPHA,
} from '../../constants';
import Pixel from './pixel';

export default class Canvas {
  constructor(matrixSize) {
    this.matrixSize = matrixSize;
    this.layers = null;
    this.globalAlphaList = null;
    this.temporaryMatrix = null;
    this.reDrawFlag = false;

    this.mainColor = DEFAULT_MAIN_COLOR;
    this.secondColor = DEFAULT_SECOND_COLOR;
    this.activeToolId = TOOL_IDS.PEN;
    this.activePixel = null;
    this.mouseDownPixel = null;
    this.mouseUpPixel = null;
    this.activeLayer = 0;
    this.len = 0;
  }

  getLen() {
    return this.len;
  }

  createMatrix(globalAlphaList = [DEFAULT_ALPHA]) {
    const arr = Array(this.matrixSize);
    this.globalAlphaList = globalAlphaList;
    this.layers = globalAlphaList.map(() => map(arr, () => map(arr, () => new Pixel())));
    this.temporaryMatrix = map(arr, () => map(arr, () => new Pixel()));
    return this;
  }

  getCopy() {
    const copy = new Canvas(this.matrixSize).createMatrix();
    copy.layers.forEach((layer, layerIndex) => {
      layer.forEach((row, indexRow) => {
        row.forEach((pixel, indexColumn) => {
          pixel.setColor(this.layers[layerIndex][indexRow][indexColumn].color);
        });
      });
    });

    return copy;
  }

  serialize() {
    return {
      matrixSize: this.matrixSize,
      layers: this.layers.map(layer => layer.map(row => row.map(({ color }) => ({ color })))),
      mainColor: this.mainColor,
      secondColor: this.secondColor,
      activeToolId: this.activeToolId,
      globalAlphaList: this.globalAlphaList,
      activeLayer: this.activeLayer,
    };
  }

  unSerialize(data) {
    if (data) {
      this.matrixSize = data.matrixSize;
      this.resetTemporaryMatrix();
      this.layers = data.layers.map(layer => layer.map(row => row.map(({ color }) => (new Pixel(color)))));
      this.mainColor = data.mainColor;
      this.secondColor = data.secondColor;
      this.activeToolId = data.activeToolId;
      this.globalAlphaList = data.globalAlphaList;
      this.activeLayer = data.activeLayer;
    }
    return this;
  }

  setActiveLayer(activeLayer) {
    this.activeLayer = activeLayer;
    this.reDrawFlag = !this.reDrawFlag;
  }

  setGlobalAlphaList(globalAlphaList) {
    this.globalAlphaList = globalAlphaList;
    this.reDrawFlag = !this.reDrawFlag;
  }

  addLayer(activeLayer, globalAlphaList) {
    const arr = Array(this.matrixSize);
    this.layers.splice(activeLayer, 0, map(arr, () => map(arr, () => new Pixel())));
    this.activeLayer = activeLayer;
    this.globalAlphaList = globalAlphaList;
    this.reDrawFlag = !this.reDrawFlag;
  }

  deleteLayer(activeLayer, globalAlphaList) {
    this.layers.splice(activeLayer, 1);
    this.activeLayer = 0;
    this.globalAlphaList = globalAlphaList;
    this.reDrawFlag = !this.reDrawFlag;
  }

  upLayer(activeLayer, globalAlphaList) {
    this.layers.splice(activeLayer - 1, 0, this.layers.splice(activeLayer, 1)[0]);
    this.activeLayer = activeLayer - 1;
    this.globalAlphaList = globalAlphaList;
    this.reDrawFlag = !this.reDrawFlag;
  }

  downLayer(activeLayer, globalAlphaList) {
    this.layers.splice(activeLayer + 1, 0, this.layers.splice(activeLayer, 1)[0]);
    this.activeLayer = activeLayer + 1;
    this.globalAlphaList = globalAlphaList;
    this.reDrawFlag = !this.reDrawFlag;
  }

  setMainColor(color) {
    this.mainColor = color;
  }

  getMainColor() {
    return this.mainColor;
  }

  setSecondColor(color) {
    this.secondColor = color;
  }

  setActiveToolId(activeToolId) {
    this.activeToolId = activeToolId;
  }

  setMatrixSize(matrixSize) {
    this.matrixSize = matrixSize;
    const arr = Array(this.matrixSize);
    this.temporaryMatrix = map(arr, () => map(arr, () => new Pixel()));

    const layers = this.layers.map((layer) => {
      const newLayer = map(arr, () => map(arr, () => new Pixel()));
      newLayer.forEach((row, indexRow) => {
        row.forEach((pixel, indexColumn) => {
          const oldPixel = get(layer, [indexRow, indexColumn]);
          if (oldPixel) {
            pixel.setColor(oldPixel.color);
          }
        });
      });
      return newLayer;
    });
    this.layers = layers;
    this.reDrawFlag = !this.reDrawFlag;
  }

  setActivePixel(activePixel) {
    this.resetTemporaryMatrix();

    if (this.mouseDownPixel) {
      switch (this.activeToolId) {
        case TOOL_IDS.STROKE:
        case TOOL_IDS.CIRCLE:
        case TOOL_IDS.RULE:
        case TOOL_IDS.RECTANGLE:
        case TOOL_IDS.SQUARE: {
          this.draw(this.mouseDownPixel, activePixel, true);
          break;
        }

        case TOOL_IDS.PEN:
        case TOOL_IDS.ERASER:
        case TOOL_IDS.DITHERING: {
          this.draw(this.activePixel, activePixel, false);
          break;
        }

        default: {
          break;
        }
      }
    } else {
      this.setActiveColor(activePixel);
    }

    this.activePixel = activePixel;
    this.reDrawFlag = !this.reDrawFlag;
  }

  setActiveColor(activePixel) {
    if (activePixel && this.isActivePixelIntoCanvas(activePixel)) {
      const { indexRow, indexColumn } = activePixel;
      const activePixelNode = this.temporaryMatrix[indexRow][indexColumn];
      activePixelNode.setColor(ACTIVE_COLOR);
    }
  }

  unSetActiveColor(activePixel) {
    if (activePixel && this.isActivePixelIntoCanvas(activePixel)) {
      const { indexRow, indexColumn } = activePixel;
      const activePixelNode = this.temporaryMatrix[indexRow][indexColumn];
      activePixelNode.setColor(INITIAL_COLOR);
    }
  }

  resetTemporaryMatrix() {
    const arr = Array(this.matrixSize);
    this.temporaryMatrix = map(arr, () => map(arr, () => new Pixel()));
  }

  removeActivePixel() {
    this.resetTemporaryMatrix();
    this.reDrawFlag = !this.reDrawFlag;
  }

  isActivePixelIntoCanvas({ indexRow, indexColumn }) {
    return indexRow < this.matrixSize && indexRow >= 0 && indexColumn < this.matrixSize && indexColumn >= 0;
  }

  setMouseDownPixel(mouseDownPixel) {
    if (mouseDownPixel && this.isActivePixelIntoCanvas(mouseDownPixel)) {
      this.mouseDownPixel = mouseDownPixel;

      switch (this.activeToolId) {
        case TOOL_IDS.PEN:
        case TOOL_IDS.ERASER:
        case TOOL_IDS.DITHERING: {
          this.draw(this.mouseDownPixel, this.mouseDownPixel, false);
          this.unSetActiveColor(mouseDownPixel);
          break;
        }

        case TOOL_IDS.STROKE:
        case TOOL_IDS.CIRCLE:
        case TOOL_IDS.RULE:
        case TOOL_IDS.RECTANGLE:
        case TOOL_IDS.SQUARE: {
          this.draw(this.mouseDownPixel, this.mouseDownPixel, true);
          break;
        }

        case TOOL_IDS.BUCKET_SC: {
          this.draw(this.mouseDownPixel, this.mouseDownPixel, true);
          this.unSetActiveColor(mouseDownPixel);
          break;
        }

        case TOOL_IDS.BUCKET: {
          this.bucket(this.mouseDownPixel);
          break;
        }

        case TOOL_IDS.PIPET: {
          this.pipet(this.mouseDownPixel);
          break;
        }

        default: {
          break;
        }
      }

      this.reDrawFlag = !this.reDrawFlag;
    }
  }

  setMouseUpPixel(mouseUpPixel) {
    this.mouseUpPixel = mouseUpPixel;

    switch (this.activeToolId) {
      case TOOL_IDS.STROKE:
      case TOOL_IDS.CIRCLE:
      case TOOL_IDS.RECTANGLE:
      case TOOL_IDS.SQUARE: {
        this.draw(this.mouseDownPixel, mouseUpPixel, false);
        break;
      }

      case TOOL_IDS.RULE: {
        this.len = 0;

        break;
      }

      default: {
        break;
      }
    }

    this.mouseDownPixel = null;
    this.setActiveColor(this.mouseUpPixel);
    this.reDrawFlag = !this.reDrawFlag;
  }

  draw(firstPixel, secondPixel, isTemporary) {
    if (firstPixel && secondPixel && this.activeToolId) {
      switch (this.activeToolId) {
        case TOOL_IDS.STROKE:
        case TOOL_IDS.PEN:
        case TOOL_IDS.ERASER:
        case TOOL_IDS.DITHERING: {
          this.drawLine(firstPixel, secondPixel, isTemporary);
          break;
        }

        case TOOL_IDS.RULE: {
          this.rule(firstPixel, secondPixel, isTemporary);
          break;
        }

        case TOOL_IDS.RECTANGLE: {
          this.drawRectangle(firstPixel, secondPixel, isTemporary);
          break;
        }

        case TOOL_IDS.SQUARE: {
          this.drawSquare(firstPixel, secondPixel, isTemporary);
          break;
        }

        case TOOL_IDS.BUCKET_SC: {
          const { indexRow, indexColumn } = firstPixel;
          const { color } = this.layers[this.activeLayer][indexRow][indexColumn];

          this.layers[this.activeLayer].forEach((row) => {
            row.forEach((pixel) => {
              if (pixel.color === color) {
                pixel.setColor(this.mainColor);
              }
            });
          });
          break;
        }

        case TOOL_IDS.CIRCLE: {
          this.drawCircle(firstPixel, secondPixel, isTemporary);
          break;
        }

        default:
          break;
      }
    }
  }

  drawLine = (
    { indexRow: x1, indexColumn: y1 },
    { indexRow: x2, indexColumn: y2 },
    isTemporary,
  ) => {
    const deltaX = Math.abs(x2 - x1);
    const deltaY = Math.abs(y2 - y1);
    const signX = (x1 < x2) ? 1 : -1;
    const signY = (y1 < y2) ? 1 : -1;

    let error = deltaX - deltaY;
    let x = x1;
    let y = y1;

    this.changePixelColor(x2, y2, isTemporary);

    while (x !== x2 || y !== y2) {
      this.changePixelColor(x, y, isTemporary);
      const error2 = error * 2;

      if (error2 > -deltaY) {
        error -= deltaY;
        x += signX;
      }
      if (error2 < deltaX) {
        error += deltaX;
        y += signY;
      }
    }
  }

  bucket = ({ indexRow, indexColumn }) => {
    const pixelStack = [[indexColumn, indexRow]];
    const { color: initColor } = this.layers[this.activeLayer][indexRow][indexColumn];

    if (this.mainColor === initColor) {
      return;
    }

    while (pixelStack.length) {
      const newPos = pixelStack.pop();
      const x = newPos[0];
      let y = newPos[1];

      while (y !== 0 && initColor === this.layers[this.activeLayer][y - 1][x].color) {
        y -= 1;
      }

      let leftFlag = true;
      let rightFlag = true;

      do {
        this.changePixelColor(y, x, false);
        if (x - 1 >= 0 && initColor === this.layers[this.activeLayer][y][x - 1].color) {
          if (leftFlag) {
            pixelStack.push([x - 1, y]);
            leftFlag = false;
          }
        } else {
          leftFlag = true;
        }
        if (x + 1 < this.matrixSize && initColor === this.layers[this.activeLayer][y][x + 1].color) {
          if (rightFlag) {
            pixelStack.push([x + 1, y]);
            rightFlag = false;
          }
        } else {
          rightFlag = true;
        }
        y += 1;
      } while (y < this.matrixSize && initColor === this.layers[this.activeLayer][y][x].color);
    }
  }

  pipet = ({ indexRow, indexColumn }) => {
    const { color } = this.layers[this.activeLayer][indexRow][indexColumn];
    this.mainColor = color;
  }

  getColorByIndexes(indexRow, indexColumn) {
    switch (this.activeToolId) {
      case TOOL_IDS.ERASER: {
        return INITIAL_COLOR;
      }

      case TOOL_IDS.DITHERING: {
        const sum = indexRow + indexColumn;
        return sum % 2 === 0 ? this.mainColor : this.secondColor;
      }

      default:
        return this.mainColor;
    }
  }

  changePixelColor(indexRow, indexColumn, isTemporary) {
    if (this.isActivePixelIntoCanvas({ indexRow, indexColumn })) {
      const matrix = isTemporary ? this.temporaryMatrix : this.layers[this.activeLayer];
      const pixelNode = matrix[indexRow][indexColumn];
      pixelNode.setColor(this.getColorByIndexes(indexRow, indexColumn));
    }
  }

  drawCircle = (
    { indexRow: x1, indexColumn: y1 },
    { indexRow: x2, indexColumn: y2 },
    isTemporary,
  ) => {
    let x = 0;
    const radius = Math.sqrt(((x2 - x1) * (x2 - x1)) + ((y2 - y1) * (y2 - y1))) / 2;
    let y = Math.ceil(radius);
    let delta = 1 - 2 * radius;
    let error = 0;
    while (y >= 0) {
      this.changePixelColor(x1 + x, y1 + y, isTemporary);
      this.changePixelColor(x1 + x, y1 - y, isTemporary);
      this.changePixelColor(x1 - x, y1 + y, isTemporary);
      this.changePixelColor(x1 - x, y1 - y, isTemporary);

      error = 2 * (delta + y) - 1;
      if (delta < 0 && error <= 0) {
        x += 1;
        delta += 2 * x + 1;
        continue;
      }
      error = 2 * (delta - x) - 1;
      if (delta > 0 && error > 0) {
        y -= 1;
        delta += 1 - 2 * y;
        continue;
      }
      x += 1;
      delta += 2 * (x - y);
      y -= 1;
    }
  };

  drawRectangle = (
    { indexRow: x1, indexColumn: y1 },
    { indexRow: x2, indexColumn: y2 },
    isTemporary,
  ) => {
    const signX = (x1 < x2) ? 1 : -1;
    const signY = (y1 < y2) ? 1 : -1;

    let x = x1;
    let y = y1;

    this.changePixelColor(x2, y2, isTemporary);

    while (x !== x2) {
      this.changePixelColor(x, y1, isTemporary);
      this.changePixelColor(x, y2, isTemporary);
      x += signX;
    }

    while (y !== y2) {
      this.changePixelColor(x1, y, isTemporary);
      this.changePixelColor(x2, y, isTemporary);
      y += signY;
    }
  }

  drawSquare = (
    { indexRow: x1, indexColumn: y1 },
    { indexRow: x2, indexColumn: y2 },
    isTemporary,
  ) => {
    const deltaX = Math.abs(x2 - x1);
    const deltaY = Math.abs(y2 - y1);
    const delta = Math.min(deltaX, deltaY);
    const signX = (x1 < x2) ? 1 : -1;
    const signY = (y1 < y2) ? 1 : -1;

    let x = x1;
    let y = y1;

    this.changePixelColor(x1 + delta * signX, y1 + delta * signY, isTemporary);

    while (x !== x2 && y !== y2) {
      this.changePixelColor(x, y1, isTemporary);
      this.changePixelColor(x, y1 + delta * signY, isTemporary);
      this.changePixelColor(x1, y, isTemporary);
      this.changePixelColor(x1 + delta * signX, y, isTemporary);
      x += signX;
      y += signY;
    }
  }

  rule = (
    { indexRow: x1, indexColumn: y1 },
    { indexRow: x2, indexColumn: y2 },
    isTemporary,
  ) => {
    const deltaX = Math.abs(x2 - x1);
    const deltaY = Math.abs(y2 - y1);
    const signX = (x1 < x2) ? 1 : -1;
    const signY = (y1 < y2) ? 1 : -1;

    let error = deltaX - deltaY;
    let x = x1;
    let y = y1;

    this.changePixelColor(x2, y2, isTemporary);

    while (x !== x2 || y !== y2) {
      this.changePixelColor(x, y, isTemporary);
      const error2 = error * 2;

      if (error2 > -deltaY) {
        error -= deltaY;
        x += signX;
      }
      if (error2 < deltaX) {
        error += deltaX;
        y += signY;
      }
    }
    this.len = (Math.sqrt(((x2 - x1) * (x2 - x1)) + ((y2 - y1) * (y2 - y1))) + 1).toFixed(2);
  }
}
