/* eslint-disable react/prop-types */
import React from 'react';
import cn from 'classnames';

import { drawCtx } from '../../helpers/draw-ctx';

import './Canvas.scss';

class Canvas extends React.PureComponent {
  canvas = null;

  componentDidMount() {
    this.draw();
  }

  componentDidUpdate(prevProps) {
    const { layers, reDrawFlag, size } = this.props;
    if (prevProps.layers !== layers || prevProps.reDrawFlag !== reDrawFlag || prevProps.size !== size) {
      this.draw();
    }
  }

  draw = () => {
    const {
      layers, temporaryMatrix, matrixSize, size, globalAlphaList, activeLayer, skipActiveLayer,
    } = this.props;
    if (layers && this.canvas) {
      drawCtx({
        canvas: this.canvas,
        layers,
        size,
        matrixSize,
        temporaryMatrix,
        globalAlphaList,
        activeLayer,
        skipActiveLayer,
      });
    }
  }

  refCanvas = (element) => {
    const { refCanvas } = this.props;
    this.canvas = element;
    if (refCanvas) {
      refCanvas(element);
    }
  }

  render() {
    const {
      className, size, onMouseLeave,
    } = this.props;

    return (
      <div className={cn(className, 'canvas-wrapper')}>
        <canvas
          onMouseLeave={onMouseLeave}
          ref={this.refCanvas}
          width={size}
          height={size}
        />
      </div>
    );
  }
}

export default Canvas;
