/* eslint-disable react/prop-types */
import React from 'react';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import OpacityIcon from '@material-ui/icons/Opacity';

import './AlphaButton.scss';

export default function AlphaButton({ title, alpha, onChangeAlpha }) {
  const [open, setOpen] = React.useState(false);
  const [value, setValue] = React.useState(alpha);

  function handleClickOpen() {
    setOpen(true);
    setValue(alpha);
  }

  function handleClose() {
    setOpen(false);
  }

  function handleChange(e) {
    const { value: val } = e.currentTarget;
    if (val >= 0 && val <= 1) {
      setValue(val);
    }
  }

  function handleOk() {
    onChangeAlpha(value);
    setOpen(false);
  }

  return (
    <>
      <IconButton className="alpha-button" color="secondary" size="small" onClick={handleClickOpen}>
        <OpacityIcon fontSize="inherit" />
      </IconButton>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">{title}</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            value={value}
            margin="dense"
            id="alpha"
            label="Alpha"
            type="number"
            onChange={handleChange}
            fullWidth
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleOk} color="primary">
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
