/* eslint-disable no-console */
/* eslint-disable react/prop-types */
import React from 'react';
import { connect } from 'react-redux';
import cn from 'classnames';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import CircularProgress from '@material-ui/core/CircularProgress';
import Snackbar from '@material-ui/core/Snackbar';

import {
  selectCanvasList,
  selectFps,
  selectActiveCanvasIndex,
  selectMatrixSize,
  selectUser,
  selectLayersTextList,
  selectGlobalAlphaList,
} from '../../store/selectors';
import { setFpsAction } from '../../store/actions';
import { download } from '../../helpers/download';
import {
  createGif,
  createPng,
  createPiskel,
  uploadFileToGoogleDrive,
  createOwn,
} from './utils';

import { KEY_CODE } from '../../constants';

import './ExportPanel.scss';

class ExportPanel extends React.PureComponent {
  state = {
    saveAnchorEl: null,
    uploadAnchorEl: null,
    openAnchorEl: null,
    saveLoading: false,
    openLoading: false,
    uploadLoading: false,
    error: null,
  }

  componentDidMount() {
    window.document.addEventListener('keydown', this.handleKeyDown);
  }

  componentWillUnmount() {
    window.document.removeEventListener('keydown', this.handleKeyDown);
  }

  handleKeyDown = (e) => {
    if (e.shiftKey && e.keyCode === KEY_CODE.KEY_1) {
      this.onSaveGif();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_2) {
      this.onSavePng();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_3) {
      this.onSavePiskel();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_4) {
      this.onSaveOwn();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_5) {
      this.onOpenGif();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_6) {
      this.onOpenPng();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_7) {
      this.onOpenPiskel();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_8) {
      this.onOpenOwn();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_9) {
      this.onUploadPngToGoogleDrive();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_0) {
      this.onUploadGifToGoogleDrive();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_minus) {
      this.onUploadPiskelToGoogleDrive();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_rule) {
      this.onUploadOwnToGoogleDrive();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_UP) {
      this.increaseFPS();
    } else if (e.shiftKey && e.keyCode === KEY_CODE.KEY_DOWN) {
      this.reduceFPS();
    }
  }

  increaseFPS = () => {
    const { fps, setFps } = this.props;
    if (fps < 24) {
      setFps(fps + 1);
    }
  }

  reduceFPS = () => {
    const { fps, setFps } = this.props;
    if (fps > 1) {
      setFps(fps - 1);
    }
  }

  onClickSave = (event) => {
    this.setState({ saveAnchorEl: event.currentTarget });
  }

  handleCloseSaveMenu = () => {
    this.setState({ saveAnchorEl: null });
  }

  onClickOpen = (event) => {
    this.setState({ openAnchorEl: event.currentTarget });
  }

  handleCloseOpenMenu = () => {
    this.setState({ openAnchorEl: null });
  }

  onClickUpload = (event) => {
    this.setState({ uploadAnchorEl: event.currentTarget });
  }

  handleCloseUploadMenu = () => {
    this.setState({ uploadAnchorEl: null });
  }

  handleCloseError = () => {
    this.setState({ error: null });
  }

  onSaveGif = () => {
    const { canvasList, fps, matrixSize } = this.props;

    this.setState({ saveLoading: true });

    createGif(canvasList, fps, matrixSize)
      .then((blob) => {
        this.setState({ saveLoading: false });
        download(URL.createObjectURL(blob), `${Date.now()}-myimage.gif`);
      })
      .catch((error) => {
        this.setState({ saveLoading: false, error });
      });

    this.handleCloseSaveMenu();
  }

  onSavePng = () => {
    const { canvasList, activeCanvasIndex, matrixSize } = this.props;

    this.setState({ saveLoading: true });

    createPng(canvasList, activeCanvasIndex, matrixSize)
      .then((blob) => {
        this.setState({ saveLoading: false });
        download(URL.createObjectURL(blob), `${Date.now()}-myimage.png`);
      })
      .catch((error) => {
        this.setState({ saveLoading: false, error });
      });

    this.handleCloseSaveMenu();
  }

  onSavePiskel = () => {
    const {
      canvasList, fps, matrixSize, layersTextList, globalAlphaList,
    } = this.props;

    this.setState({ saveLoading: true });

    createPiskel(canvasList, fps, matrixSize, layersTextList, globalAlphaList)
      .then((blob) => {
        this.setState({ saveLoading: false });
        download(URL.createObjectURL(blob), `${Date.now()}-myimage.piskel`);
      })
      .catch((error) => {
        this.setState({ saveLoading: false, error });
      });

    this.handleCloseSaveMenu();
  }

  onSaveOwn = () => {
    const { state } = this.props;

    this.setState({ saveLoading: true });

    createOwn(state)
      .then((blob) => {
        this.setState({ saveLoading: false });
        download(URL.createObjectURL(blob), `${Date.now()}-myimage.mypiskel`);
      })
      .catch((error) => {
        this.setState({ saveLoading: false, error });
      });

    this.handleCloseSaveMenu();
  }

  onOpenGif = () => {
    const { canvasList, fps, matrixSize } = this.props;
    this.setState({ openLoading: true });
    createGif(canvasList, fps, matrixSize)
      .then((blob) => {
        this.setState({ openLoading: false });
        window.open(URL.createObjectURL(blob));
      })
      .catch((error) => {
        this.setState({ openLoading: false, error });
      });

    this.handleCloseOpenMenu();
  }

  onOpenPng = () => {
    const { canvasList, activeCanvasIndex, matrixSize } = this.props;
    this.setState({ openLoading: true });
    createPng(canvasList, activeCanvasIndex, matrixSize)
      .then((blob) => {
        this.setState({ openLoading: false });
        window.open(URL.createObjectURL(blob));
      })
      .catch((error) => {
        this.setState({ openLoading: false, error });
      });

    this.handleCloseOpenMenu();
  }

  onOpenPiskel = () => {
    const {
      canvasList, fps, matrixSize, layersTextList, globalAlphaList,
    } = this.props;
    this.setState({ openLoading: true });
    createPiskel(canvasList, fps, matrixSize, layersTextList, globalAlphaList)
      .then((blob) => {
        this.setState({ openLoading: false });
        window.open(URL.createObjectURL(blob));
      })
      .catch((error) => {
        this.setState({ openLoading: false, error });
      });

    this.handleCloseOpenMenu();
  }

  onOpenOwn = () => {
    const { state } = this.props;
    this.setState({ openLoading: true });
    createOwn(state)
      .then((blob) => {
        this.setState({ openLoading: false });
        window.open(URL.createObjectURL(blob));
      })
      .catch((error) => {
        this.setState({ openLoading: false, error });
      });

    this.handleCloseOpenMenu();
  }

  onUploadPngToGoogleDrive = () => {
    const { canvasList, activeCanvasIndex, matrixSize } = this.props;
    this.setState({ uploadLoading: true });
    createPng(canvasList, activeCanvasIndex, matrixSize)
      .then(blob => uploadFileToGoogleDrive(`${Date.now()}-myimage.png`, 'image/png', blob))
      .then((data) => {
        this.setState({ uploadLoading: false });
        window.open(`https://drive.google.com/file/d/${data.id}/view`);
      })
      .catch((error) => {
        this.setState({ uploadLoading: false, error });
      });

    this.handleCloseUploadMenu();
  }

  onUploadGifToGoogleDrive = () => {
    const { canvasList, fps, matrixSize } = this.props;
    this.setState({ uploadLoading: true });
    createGif(canvasList, fps, matrixSize)
      .then(blob => uploadFileToGoogleDrive(`${Date.now()}-myimage.gif`, 'image/gif', blob))
      .then((data) => {
        this.setState({ uploadLoading: false });
        window.open(`https://drive.google.com/file/d/${data.id}/view`);
      })
      .catch((error) => {
        this.setState({ uploadLoading: false, error });
      });

    this.handleCloseUploadMenu();
  }

  onUploadPiskelToGoogleDrive = () => {
    const {
      canvasList, fps, matrixSize, layersTextList, globalAlphaList,
    } = this.props;
    this.setState({ uploadLoading: true });
    createPiskel(canvasList, fps, matrixSize, layersTextList, globalAlphaList)
      .then(blob => uploadFileToGoogleDrive(`${Date.now()}-myimage.piskel`, 'application/json', blob))
      .then((data) => {
        this.setState({ uploadLoading: false });
        window.open(`https://drive.google.com/file/d/${data.id}/view`);
      })
      .catch((error) => {
        this.setState({ uploadLoading: false, error });
      });

    this.handleCloseUploadMenu();
  }

  onUploadOwnToGoogleDrive = () => {
    const { state } = this.props;
    this.setState({ uploadLoading: true });
    createOwn(state)
      .then(blob => uploadFileToGoogleDrive(`${Date.now()}-myimage.mypiskel`, 'application/json', blob))
      .then((data) => {
        this.setState({ uploadLoading: false });
        window.open(`https://drive.google.com/file/d/${data.id}/view`);
      })
      .catch((error) => {
        this.setState({ uploadLoading: false, error });
      });

    this.handleCloseUploadMenu();
  }

  render() {
    const { user } = this.props;
    const {
      saveAnchorEl,
      uploadAnchorEl,
      openAnchorEl,
      saveLoading,
      openLoading,
      uploadLoading,
      error,
    } = this.state;

    return (
      <div className={cn('export-panel-wrapper')}>
        <div className="button-wrapper">
          <Button
            onClick={this.onClickSave}
            variant="contained"
            color="primary"
            className={cn('button', { disabled: saveLoading })}
            aria-controls="simple-menu"
            aria-haspopup="true"
          >
            Save
          </Button>
          {saveLoading && <CircularProgress size={24} className="button-progress" />}
        </div>

        <Menu
          id="simple-menu"
          anchorEl={saveAnchorEl}
          keepMounted
          open={Boolean(saveAnchorEl)}
          onClose={this.handleCloseSaveMenu}
        >
          <MenuItem onClick={this.onSaveGif}>GIF</MenuItem>
          <MenuItem onClick={this.onSavePng}>PNG</MenuItem>
          <MenuItem onClick={this.onSavePiskel}>.piskel</MenuItem>
          <MenuItem onClick={this.onSaveOwn}>.mypiskel</MenuItem>
        </Menu>

        <div className="button-wrapper">
          <Button
            onClick={this.onClickOpen}
            variant="contained"
            color="primary"
            className={cn('button', { disabled: openLoading })}
            aria-controls="simple-menu"
            aria-haspopup="true"
          >
            Open
          </Button>
          {openLoading && <CircularProgress size={24} className="button-progress" />}
        </div>

        <Menu
          id="simple-menu"
          anchorEl={openAnchorEl}
          keepMounted
          open={Boolean(openAnchorEl)}
          onClose={this.handleCloseOpenMenu}
        >
          <MenuItem onClick={this.onOpenGif}>GIF</MenuItem>
          <MenuItem onClick={this.onOpenPng}>PNG</MenuItem>
          <MenuItem onClick={this.onOpenPiskel}>.piskel</MenuItem>
          <MenuItem onClick={this.onOpenOwn}>.mypiskel</MenuItem>
        </Menu>

        <div className="button-wrapper">
          <Button
            onClick={this.onClickUpload}
            disabled={!user}
            variant="contained"
            color="primary"
            className={cn('button', { disabled: !user || uploadLoading })}
            aria-controls="simple-menu"
            aria-haspopup="true"
          >
            Upload to google drive
          </Button>
          {uploadLoading && <CircularProgress size={24} className="button-progress" />}
        </div>

        <Menu
          id="simple-menu"
          anchorEl={uploadAnchorEl}
          keepMounted
          open={Boolean(uploadAnchorEl)}
          onClose={this.handleCloseUploadMenu}
        >
          <MenuItem onClick={this.onUploadGifToGoogleDrive}>GIF</MenuItem>
          <MenuItem onClick={this.onUploadPngToGoogleDrive}>PNG</MenuItem>
          <MenuItem onClick={this.onUploadPiskelToGoogleDrive}>.piskel</MenuItem>
          <MenuItem onClick={this.onUploadOwnToGoogleDrive}>.mypiskel</MenuItem>
        </Menu>

        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={!!error}
          autoHideDuration={6000}
          onClose={this.handleCloseError}
          ContentProps={{
            'aria-describedby': 'message-id',
          }}
          message={<span id="message-id">{`Error: ${error && error.message}`}</span>}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  canvasList: selectCanvasList(state),
  fps: selectFps(state),
  activeCanvasIndex: selectActiveCanvasIndex(state),
  matrixSize: selectMatrixSize(state),
  user: selectUser(state),
  layersTextList: selectLayersTextList(state),
  globalAlphaList: selectGlobalAlphaList(state),
  state,
});

export default connect(mapStateToProps, { setFps: setFpsAction })(ExportPanel);
